  margin-right: 1rem; }

textarea {
  resize: vertical; }

input[readonly] {
  border: none;
  border-bottom: 1px solid #e0e0e0;
  color: #616161; }
  input[readonly]:focus {
    outline: none; }

.checkbox__container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }
  .checkbox__container:hover input ~ .checkbox__mark {
    background-color: #d32f2f; }
  .checkbox__container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0; }
    .checkbox__container input:checked ~ .checkbox__mark {
      background-color: #d32f2f; }
      .checkbox__container input:checked ~ .checkbox__mark:after {
        display: block; }
    .checkbox__container input.input__error + .checkbox__mark {
      border-color: #f44336; }
  .checkbox__container .checkbox__mark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    border: 1px solid #e0e0e0;
    background-color: #eee; }
    .checkbox__container .checkbox__mark:after {
      content: "";
      position: absolute;
      display: none;
      left: 9px;
      top: 5px;
      width: 5px;
      height: 10px;
      border: solid white;
      border-width: 0 3px 3px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg); }

.form__group {
  position: relative;
  margin: 1rem 0; }
  .form__group .form-control {
    display: flex;
    align-items: stretch;
    justify-content: stretch; }
    .form__group .form-control .after {
      padding: 0 1.5rem;
