/********************
/* S| Modal
/******************* */
/* Modal Content/Box */
.modal {
  display: none;
  position: fixed;
  background-color: #fefefe;
  margin: auto;
  /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #e0e0e0;
  width: 40%;
  /* Could be more or less, depending on screen size */
  top: 15%;
  left: calc(-50vw + 50%);
  right: calc(-50vw + 50%);
  z-index: 10000;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px; }
  .modal + .backdrop {
    display: none; }
  .modal.open {
    display: block; }
    .modal.open + .backdrop {
      display: block;
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      background: rgba(0, 0, 0, 0.5);
      z-index: 1000; }
  .modal.modal--small {
    margin: 0 !important;
    left: unset;
    top: unset;
    right: 1rem;
    bottom: 1rem;
    width: 50%;
    border-color: transparent;
    background: #616161;
    color: #fff;
    transform: unset; }

@media screen and (max-width: 1199px) {
  .modal {
    width: 80%; } }

